* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Pretendard, sans-serif;
}

body {
  background-color: #585858;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  display: flex;
}

.wrapper {
  backdrop-filter: blur(20px);
  box-shadow: 0 0 10px rgba (0, 0, 0, .2);
  color: #fff;
  background: none;
  border: 2px solid #fff3;
  border-radius: 10px;
  width: 420px;
  padding: 30px 40px;
}

.wrapper h1 {
  text-align: center;
  font-size: 36px;
}

.wrapper .input-box {
  width: 100%;
  height: 50px;
  margin: 30px 0;
  position: relative;
}

.input-box input {
  color: #fff;
  background-color: #0000;
  border: 2px solid #fff3;
  border-radius: 40px;
  outline: none;
  width: 100%;
  height: 100%;
  padding: 20px 45px 20px 20px;
  font-size: 16px;
}

.input-box input::placeholder {
  color: #fff;
}

.input-box i {
  font-size: 20px;
  position: absolute;
  top: 50%;
  right: 20px;
  transform: translateY(-50%);
}

.wrapper .errortext {
  text-align: center;
  margin: -15px 0 15px;
  font-size: 14.5px;
}

.errortext label input {
  accent-color: #fff;
  margin-right: 3px;
}

.errortext a {
  color: #fff;
  text-decoration: none;
}

.errortext a:hover {
  text-decoration: underline;
}

.wrapper .btn {
  box-shadow: 0 0 10px rgb(0, 0, 0 .1);
  cursor: pointer;
  color: #333;
  background-color: #fff;
  border: none;
  border-radius: 40px;
  outline: none;
  width: 100%;
  height: 45px;
  font-size: 16px;
  font-weight: 600;
}

.wrapper .register-link {
  text-align: center;
  margin: 20px 0 15px;
  font-size: 14.5px;
}

.register-link p a {
  color: #fff;
  font-weight: 600;
  text-decoration: none;
}

.register-link p a:hover {
  text-decoration: underline;
}
